import { socket } from "../SocketIO";
import eventManager from "./EventManager";
// eslint-disable-next-line
import {
    LoginResults,
    RegisterResults,
    ServerInfo,
    HomeInfo,
    ActiveVillage
} from "./Types";

class Client {

    ready = false;
    authorized = false;
    response = {};
    uid = window.localStorage.getItem("uid") || "";
    key = window.localStorage.getItem("key") || "";
    serverNumber = -1;

    constructor() {
        eventManager.create("ready");
        eventManager.create("authorization_updated");
    }

    // -- POST
    /** @returns {Promise<LoginResults>} */
    login(data = {}) {
        return socket.request("login", data);
    }
    /** @returns {Promise<RegisterResults>} */
    register(data = {}) {
        return socket.request("register", data);
    }

    /**@param {function(ServerInfo[]): void} onLoad */
    listServers(onLoad) {
        socket.cached("listServers", {}, onLoad, onLoad);
    }
    /**@param {function(HomeInfo): void} onLoad */
    homePage(onLoad) {
        return socket.cached("homePage", {}, onLoad, onLoad);
    }
    /**@param {function(ActiveVillage): void} onLoad */
    village(onLoad) {
        socket.cached("village", {}, onLoad, onLoad);
    }

    join(uid = "", server = -1, authorized = false) {
        this.uid = uid;
        this.serverNumber = server;
        this.authorized = authorized;
        window.localStorage.setItem("uid", uid);
        this.ready = true;
        eventManager.trigger("authorization_updated");
    }
    /** @param {Number} server * @param {Boolean} authorized * @param {String} key */
    updateAuthorization(server, authorized, key) {
        this.serverNumber = server;
        this.authorized = authorized;
        this.key = key;
        window.localStorage.setItem("key", key);
    }
    setState(authorized = false, uid = "", key = "") {
        this.authorized = authorized;
        this.uid = uid;
        this.key = key;
        window.localStorage.setItem("uid", uid);
        window.localStorage.setItem("key", key);
        eventManager.trigger("authorization_updated");
    }
}

const client = new Client();

export default client;