import React from "react";

export default class Pure extends React.PureComponent {
    componentIsReady = false;
    componentDidMount() {
        this.componentIsReady = true;
        this.onReady();
    }
    componentWillUnmount() {
        this.componentIsReady = false;
        this.onRemoved();
    }
    onReady() { }
    onRemoved() { }
    updateState(data = {}) {
        if (this.componentIsReady)
            this.setState(data);
    }
}