import { io } from "socket.io-client";
// import { io, Socket } from "socket.io-client";
// import milliseconds from "./Functions/milliseconds";
import eventManager from "./CLASSES/EventManager";
import client from "./CLASSES/client";


const ENDPOINT = "https://nalrashidi.herokuapp.com/";
// const ENDPOINT = "http://192.168.1.3:3001/";
// const ENDPOINT = "http://192.168.100.122:7741/";
const PING_RATE = 1000;
// const SOCKET_TIMEOUT = milliseconds(0, 0, 10, 0);

class GameSocket {
    //**@type {Socket<DefaultEventsMap, DefaultEventsMap>} */
    socket = null;
    callbacks = {};
    registers = {};
    responseCache = {};
    responseTime = 0;
    onReady = null;

    connect() {
        if (this.socket !== null)
            this.socket.disconnect();
        this.socket = io(ENDPOINT);
        this.socket.on("connection_done", () => {
            if (typeof this.onReady === "function")
                this.onReady();
            this.calculatePing();
        });
    }

    send(name = "", data = {}) {
        this.socket.emit(name + "_client", { uid: client.uid, key: client.key, data });
    }

    /**@returns {Promise<{}>} */
    wait(name = "") {
        return new Promise(resolve => {
            this.onResponse(name, resolve);
        });
    }

    /**@returns {Promise<{}>} */
    request(name = "", data = {}) {
        this.send(name, data);
        return this.wait(name);
    }

    /** @param {function({}): void} hasCache * @param {function({}): void} hasUpdated * @returns {Promise<{}>} */
    async cached(name = "", data = {}, hasCache, hasUpdated) {
        if (typeof this.responseCache[name] !== "undefined" && typeof hasCache === "function") {
            console.time(`Cache[${name}]`);
            hasCache(this.responseCache[name]);
            console.timeEnd(`Cache[${name}]`);
        }
        console.time(`Response[${name}]`);
        return this.request(name, data).then(response => {
            console.timeEnd(`Response[${name}]`);
            hasUpdated(response);
        });
    }

    calculatePing() {
        // CALCULATE PING TIME:
        let msTime = 0;
        let ping = () => {
            msTime = performance.now();
            this.socket.emit("ping");
        };
        this.socket.on("pong", () => {
            this.responseTime = performance.now() - msTime;
            setTimeout(ping, PING_RATE);
        });
        ping();
    }

    onResponse(name = "", callback) {
        this.callbacks[name] = callback;
        if (this.registers[name]) return;
        this.registers[name] = true;
        this.socket.on(name + "_client_response", response => {
            let updated = client.authorized !== response.authorized;
            client.authorized = response.authorized;
            if (updated)
                eventManager.trigger("authorization_updated");
            if (typeof this.callbacks[name] === "function")
                this.callbacks[name](response);
            this.responseCache[name] = response;
        })
    }
}

const socket = new GameSocket();

export { socket };
