import React from "react";
import { LinearProgress } from "@material-ui/core";
import Pure from "../Pure";

const Loading = {
    show: () => { },
    hide: () => { }
}

class ProgressBar extends Pure {
    state = { visiable: this.props.visiable || false }
    onReady() {
        if (this.props.fallback) return;
        Loading.show = () => this.show();
        Loading.hide = () => this.hide();
    }
    onRemoved() {
        if (this.props.fallback) return;
        Loading.show = () => { };
        Loading.hide = () => { };
    }
    show() {
        if (this.props.fallback) return;
        this.updateState({ visiable: true });
    }
    hide() {
        if (this.props.fallback) return;
        this.updateState({ visiable: false });
    }
    render() {
        return <LinearProgress className="ProgressBar" style={{ display: this.state.visiable ? "" : "none" }} />;
    }
}

export { ProgressBar, Loading }