import React from 'react';
import {
  BrowserRouter as Router,
  // Switch,
  // Route,
  // Link
} from "react-router-dom";
import './App.css';

import Header from './components/Header';
import SideNavi from './components/SideNavi';
import Content from './components/Content';
import SideInfo from './components/SideInfo';
import Footer from './components/Footer';

import { Container, Row, Col } from "react-bootstrap";
import Pure from './components/Pure';
import client from './components/CLASSES/client';
import { socket } from './components/SocketIO';

class App extends Pure {
  state = {
    small: window.innerWidth < 970,
    visible: false,
    connected: false,
    ready: false,
    authorized: false,
  }
  constructor(props) {
    super(props);
    window.addEventListener("load", () => { this.updateVisibility(); });
    window.addEventListener("scroll", () => { this.updateVisibility(); });
    window.addEventListener("resize", () => {
      if (!this.componentIsReady) return;
      let w = window.innerWidth;
      let s = this.state.small;
      if (w < 970 && !s)
        this.setState({ small: true });
      else if (w > 970 && s)
        this.setState({ small: false });
    });
    socket.onReady = () => {
      console.log("connected");
      socket.request("join").then(response => {
        if (!this.componentIsReady) return;
        console.log("joined");
        let server = response.server;
        let authorized = response.authorized;
        client.join(response.uid, server, authorized);
        this.setState({ ready: true, authorized });
      });
    };
    socket.connect();
  }

  get isAtTheBottom() {
    return (window.innerHeight + window.scrollY) >= document.body.clientHeight - 20;
  }

  updateVisibility() {
    if (!this.componentIsReady) return;
    let v = this.state.visible;
    let b = this.isAtTheBottom;
    if (v && !b)
      this.setState({ visible: false });
    else if (!v && b)
      this.setState({ visible: true });
  }

  render() {
    let props = { ready: this.state.ready, authorized: this.state.authorized }
    return <Router>
      <Container className="not-from-work" fluid>
        <Row>
          <Col aria-colspan={3}>
            <Header {...props} />
          </Col>
        </Row>
        <Row className="middle-row">
          <Col className="navi-col" style={{ display: this.state.small ? "none" : "" }}><SideNavi {...props} /></Col>
          <Col className="middle-col" style={{ minWidth: this.state.small ? 0 : 650 }}><Content {...props} /></Col>
          <Col className="side-col" style={{ display: this.state.small ? "none" : "" }}><SideInfo {...props} /></Col>
        </Row>
        <Row className="footer-row fixed-bottom" style={{ visibility: this.state.visible ? "visible" : "hidden" }}>
          <Col aria-colspan={3}>
            <Footer />
          </Col>
        </Row>
      </Container>
    </Router>;
  }
}

export default App;
