class EventManager {
    constructor() {
        this.events = {};
    }
    create(name = "") {
        if (this.events[name] != null) return;
        this.events[name] = new window.Event(name);
    }
    trigger(name = "") {
        window.dispatchEvent(this.events[name]);
    }
    waitFor(name = "", then = () => { }, once = true) {
        return new Promise(resolve => {
            window.addEventListener(name, () => {
                resolve(then());
            }, { once });
        });
    }
    listen(name = "", fn = () => { }, once = false) {
        window.addEventListener(name, fn, { once });
    }
}

const eventManager = new EventManager();

export default eventManager;