import React, { Suspense } from 'react';
import { Link, Switch, Route, Redirect } from "react-router-dom";
import {
    Navbar, Nav,
    // NavDropdown, Form, FormControl,
    Button, OverlayTrigger, Tooltip
    // Container, Row, Col
} from 'react-bootstrap';
import Signal from '@material-ui/icons/SignalCellularAlt';
import "./Header.css";
import client from './CLASSES/client';
import villageMap1 from "../images/villageMap1.png";
import villageCenter1 from "../images/villageCenter1.png";
import headerMap from "../images/map.png";
import headerStats from "../images/stats.png";
// import headerMessage from "../images/mr-1.png";
// import headerReport from "../images/mr-2.png";
import Pure from './Pure';
import { socket } from './SocketIO';
import { Pages } from '../StaticData';


class Header extends React.PureComponent {
    render() {
        return <div className="header">
            <div className="top_header"><div className="game_header" alt="header"></div></div>
            <Navbar variant="light" className="t-navbar">
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mx-auto">
                        <div className="header-items">
                            <HeaderNav ready={this.props.ready} />
                            <span className="server-time">
                                <Signal fontSize="small" htmlColor="#888888" />
                                <ServerResponseTime />
                            </span>
                        </div>
                        {/* <Nav.Link href="#home">Home</Nav.Link>
                    <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                    </NavDropdown> */}
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </div>
    }
}

class HeaderNav extends Pure {
    render() {
        if (!this.props.ready)
            return <></>;
        return <Suspense>
            <Switch>
                <Route exact path={Pages.requireAuthorization}>
                    <PlayerHeader />
                </Route>
                <Route>
                    <Link className="nav-link" to="/Home"><Button variant={"outline-dark"}>الرئيسية</Button></Link>
                    <Link className="nav-link" to="/Login"><Button variant={"outline-dark"}>الدخول</Button></Link>
                    <Link className="nav-link" to="/Register"><Button variant={"outline-dark"}>التسجيل</Button></Link>
                    <Link className="nav-link" to="/Rules"><Button variant={"outline-dark"}>التعليمات</Button></Link>
                </Route>
            </Switch>
        </Suspense>
    }
}

class ServerResponseTime extends Pure {
    interval = 0;
    state = { responseTime: 0 };
    onReady() {
        this.interval = setInterval(() => this.updateState({ responseTime: Math.floor(socket.responseTime) }), 1000);
    }
    onRemoved() {
        clearInterval(this.interval);
    }
    render() {
        return <span>{this.state.responseTime}ms</span>
    }
}


class PlayerHeader extends React.PureComponent {
    render() {
        console.log("PlayerHeader auth = " + client.authorized);
        if (!client.authorized)
            return <Redirect to="/Home" />;
        else
            return <>
                <span className="server-number" dir="rtl">السيرفر {client.serverNumber}</span>
                <PlayerIcons />
                <div className="header-mr">
                    {/* <img src={headerReport} alt="التقارير" className="header-images" />
                <img src={headerMessage} alt="الرسائل" className="header-images" /> */}
                    <OverlayTrigger placement="top" overlay={<Tooltip id={"nav-tooltip-5"}>التقارير</Tooltip>}>
                        <Link to="/Reports"></Link>
                    </OverlayTrigger>
                    <OverlayTrigger placement="top" overlay={<Tooltip id={"nav-tooltip-6"}>الرسائل</Tooltip>}>
                        <Link to="/Messages"></Link>
                    </OverlayTrigger>
                </div>
            </>;
    }
}

class PlayerIcons extends Pure {
    state = { update: 0 };
    update() {
        this.updateState({ update: Date.now() });
    }
    render() {
        return <>
            <NavigationImage onClick={() => this.update()} href="/Village" id="1" title="خريطة القرية" src={villageMap1} />
            <NavigationImage onClick={() => this.update()} href="/Center" id="2" title="مركز القرية" src={villageCenter1} />
            <NavigationImage onClick={() => this.update()} href="/Map" id="3" title="الخريطة" src={headerMap} />
            <NavigationImage onClick={() => this.update()} href="/Statistics" id="4" title="الاحصائيات" src={headerStats} />
        </>;
    }
}

/** @augments {Pure<{ href: String, id: String, title: String, src: String, data: String, onClick: function(): void }>} */
class NavigationImage extends Pure {
    render() {
        return <Link to={this.props.href} onClick={() => this.props.onClick()}>
            <OverlayTrigger placement="top" overlay={<Tooltip id={"nav-tooltip-" + this.props.id}>{this.props.title}</Tooltip>}>
                <img src={this.props.src} alt={this.props.title} className={"header-images" + (window.location.pathname === this.props.href ? " active" : "")} />
            </OverlayTrigger>
        </Link>
    }
}

export default Header;